import { Component, OnInit, Inject } from '@angular/core';
import { environment } from '../../../environments/environment';
import { Router } from '@angular/router';
import { PaymentSwService } from '../../services/payment-sw/payment-sw.service';
import { CurrencySelectionService } from '../../services/currency-selection/currency-selection.service';
import { APP_BASE_HREF, PlatformLocation } from '@angular/common';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-payment-selection',
  templateUrl: './payment-selection.component.html',
  styleUrls: ['./payment-selection.component.scss']
})
export class PaymentSelectionComponent implements OnInit {

  private readonly BOOTSTRAP_MAX_COL = 12;
  private readonly MIN_COL_SIZE = 3;

  public selectedCurrency = null;

  get baseHref(): string {
    return this.platformLocation.getBaseHrefFromDOM();
  }

  public supportedCurrencies = this.sw.augmentedSwMessage.pipe(
    map(({ acceptedTokens }) => {
      const currencyReg = /^[t|T]?(.*)$/;
      // return environment.supportedCurrencies;
      return environment.supportedCurrencies
        .filter(
          (x) => [...acceptedTokens, { ticker: 'BTC' }, { ticker: 'LTC' }, { ticker: 'BCH' }]
            .find(
              (y) => y.ticker.toUpperCase() === currencyReg.exec(x.ticker)[1].toUpperCase()
            )
        );
    })
  );
  public colSize = this.supportedCurrencies.pipe((map((results) => Math.max(this.BOOTSTRAP_MAX_COL / results.length, this.MIN_COL_SIZE))));

  constructor(
    private router: Router,
    private selectionService: CurrencySelectionService,
    private sw: PaymentSwService,
    private platformLocation: PlatformLocation,
  ) { }

  ngOnInit() {
  }

  public select(currency: string) {
    this.selectedCurrency = currency;
  }

  public submit() {
    this.router.navigate(['/payment']).then(() => {
      this.selectionService.select(this.selectedCurrency.ticker);
    });
  }

}
