import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { KeyFactoryService } from '../../services/key-factory/key-factory.service';
import { map, switchMap } from 'rxjs/operators';
import { PaymentSwService } from '../../services/payment-sw/payment-sw.service';
import { ValidationService } from '../../services/validation/validation.service';
import { Router } from '@angular/router';
import { MetamaskHelperService } from 'interblockchain-components';
import { QrCodeHelperService } from 'interblockchain-components';
import { environment } from '../../../environments/environment';
import { PlatformLocation } from '@angular/common';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.scss'],
  providers: [MetamaskHelperService, QrCodeHelperService]
})
export class PaymentComponent implements OnInit {

  public error = false;

  @ViewChild('addressInput')
  addressInput: ElementRef;

  get baseHref(): string {
    return this.platformLocation.getBaseHrefFromDOM();
  }

  public currentTransaction = this.sw.currentTransaction.pipe(
    map(({ toAddress, amount, network, ...rest }) => {
      return {
        toAddress,
        amount,
        network,
        ...rest,
        qrCode: this.qrCodeHelper.getQrString(network, toAddress, amount),
      };
    }));

  public logo = this.currentTransaction.pipe(
    map((x) => environment.supportedCurrencies.find((w => w.ticker.toUpperCase() === x.converted.currency.toUpperCase())).logo)
  );

  isMetamask = this.currentTransaction.pipe(map(({ converted: { currency } }) => this.metamaskHelper.isSupported(currency)));

  isQrCode = this.currentTransaction.pipe(map(({ converted: { currency } }) => {
    return this.qrCodeHelper.isSupported(currency);
  }));

  constructor(
    private sw: PaymentSwService,
    private metamaskHelper: MetamaskHelperService,
    private qrCodeHelper: QrCodeHelperService,
    private platformLocation: PlatformLocation
  ) { }

  ngOnInit() {
    this.sw.currentValidation.subscribe(() => {
      this.sw.confirmPayment();
    });
  }

  copy() {
    this.addressInput.nativeElement.select();
    document.execCommand('copy');
  }

  payWithMetamask() {
    this.currentTransaction.subscribe((tx) => {
      this.metamaskHelper.payWithERC20({
        amount: tx.amount,
        sourceAddress: tx.toAddress,
        ticker: tx.converted.currency,
      }).catch((err) => {
        console.error(err);
        this.error = true;
      });
    });
  }
}
