import { Injectable } from '@angular/core';
import {
  ValidatorFactory,
  WatcherProxy,
  CachedWatcherProxy,
  ISingleValidationRequest,
  CombinedWatcherProxy
} from '@interblockchain/validator/dist/browser';
import { Validator } from '@interblockchain/validator/dist/validator/validator';
import { environment } from '../../../environments/environment';
import { uuidv4 } from '../../utils';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ValidationService {
  private validator: Validator;

  constructor() {
    const utxoConfig = ({
      url: environment.utxoWatcherUrl,
      params: { headers: { apiKey: environment.watchersApiKey } }
    });

    const accountConfig = ({
      url: environment.accountWatcherUrl,
      params: { headers: { apiKey: environment.watchersApiKey } }
    });

    const utxoWatcherProxy = new WatcherProxy(environment.wsProxyUrl, () => localStorage.getItem('appID'), utxoConfig);
    const accountWatcherProxy = new WatcherProxy(environment.wsProxyUrl, () => localStorage.getItem('appID'), accountConfig);

    const watcherProxy = new CombinedWatcherProxy([
      {
        proxy: utxoWatcherProxy,
        supportedNetwork: ['tBTC', 'tLTC', 'tETH', 'BTC', 'LTC', 'ETH']
      },
      {
        proxy: accountWatcherProxy,
        supportedNetwork: ['tXRP', 'tXLM', 'XLM', 'XRP']
      }
    ]);

    const cachedWatcherProxy = new CachedWatcherProxy(watcherProxy);
    this.validator = ValidatorFactory.create(cachedWatcherProxy);
  }

  watch(request: ISingleValidationRequest) {
    return this.validator.validateSingleTransaction(request);
  }
}
